import PhotoGrids from './PhotoGrids';
import Pagination from './Pagination';
import { useState } from 'react';
import content from './content';

const Home=()=>{
    const [articleInfo,setArticleInfo] = useState(content);

    const [page,setPage]=useState(1);

    const totalPage=Math.floor(articleInfo.length/6)+1;

    const ArticleEvent={
        'filt':(e,topic)=>{
            e.target.parentNode.childNodes.forEach((n)=>n.style.backgroundColor="transparent");
            e.target.style.backgroundColor="#ccc";
            let collectfilted=[];
            for (let i=0;i<content.length;i++){
                if(topic ===content[i].topic){
                    collectfilted.push(content[i])
                }
            }
            setArticleInfo(collectfilted)
        },
        'all':(e)=>{
            setArticleInfo(content);
            e.target.parentNode.childNodes.forEach((n)=>n.style.backgroundColor="transparent");
            e.target.style.backgroundColor="#ccc";
        },
        'pagenub':(page)=>{
        return(
            articleInfo.slice(-6*page,articleInfo.length-(6*(page-1))).reverse()
        )
        },
    }

    return(
        <div>
            <div className="w3-container">
                <div className="w3-section w3-bottombar w3-padding-16">
                <span className="w3-margin-right">topics:</span>
                <button id="btn_all" className="w3-button" style={{backgroundColor:"#ccc"}} onClick={(e)=>{ArticleEvent.all(e)}}>ALL</button>
                <button id="btn_Programming" className="w3-button" onClick={(e)=>{ArticleEvent.filt(e,"Programming")}}>Programming</button>
                <button id="btn_Hardware" className="w3-button" onClick={(e)=>{ArticleEvent.filt(e,"Hardware")}}>Hardware</button>
                <button id="btn_Others" className="w3-button" onClick={(e)=>{ArticleEvent.filt(e,"Others")}}>Others</button>
                </div>
            </div>
            <PhotoGrids ArticleList={ArticleEvent.pagenub(page)}/>
            <Pagination CurrentPage={page} totalPage={totalPage} setPage={(i)=>setPage(i)}/>
        </div>
    )
}

export default Home