import PhotoGrid from "./PhotoGrid"

const PhotoGrids = ({ArticleList}) => {
    return (
        <div>
            <div className="w3-row-padding">
                {ArticleList.map((Article)=>{
                    return<PhotoGrid key={Article.index} Article={Article}/>
                })
                
                }
            </div>
        </div>
    )
}

export default PhotoGrids