const PhotoGrid = ({Article}) => {

    return (
        <div className="w3-third w3-container w3-margin-bottom">
            <a href={"#/Article/"+Article.index} style={{textDecoration:"none"}}>
                <img src={Article.imgPath} alt={Article.date} style={{ width: "100%",}} className="w3-hover-opacity" />
                <div style={{height:"7rem",overflow: "hidden"}} className="w3-container w3-white">
                    <p><b>{Article.title}</b></p>
                    <p>{Article.intro}</p>
                </div>
            </a>
        </div>
    )
}
export default PhotoGrid