import './App.css'
import SideBar from './SideBar';
import Header from './Header'
import Footer from './Footer';



const App = (props) => {


  const SideFunction={
    'side_open':()=>{
      document.getElementById("mySidebar").style.display = "block";
      document.getElementById("myOverlay").style.display = "block";
    },
    'side_close':()=>{
      document.getElementById("mySidebar").style.display = "none";
      document.getElementById("myOverlay").style.display = "none";
    }
  }

  return (
    <div>
      <SideBar SideFunction={SideFunction}/>
      <div className="w3-main" style={{marginLeft:"300px"}}>
        <Header SideFunction={SideFunction}/>
        
        {props.children}

        <div className="w3-container w3-padding-large" style={{marginBottom:"32px"}}>
          <h4><b>About Me</b></h4>
            <p>I specialize in material science, interest in programming and hardware designing.</p>
          <hr/>
          <Footer/>
        </div>
      </div>
    </div>
  )
}











export default App;
