const SideBar=({SideFunction})=>{

    return(
        <div>
            <nav className="w3-sidebar w3-collapse w3-white w3-animate-left" style={{zIndex:"3" ,width:"300px"}} id="mySidebar"><br />
                <div className="w3-container">
                    <span onClick = {()=>SideFunction.side_close()} className="w3-hide-large w3-right w3-jumbo w3-padding w3-hover-grey" title="close menu">
                        <i className="fa fa-remove"></i>
                    </span>
                        <img alt="myphoto" src="a.jpg" style={{width:"45%"}} className="w3-round" /><br /><br />
                        <h4><b>DONCE's WEB</b></h4>
                </div>
                <div className="w3-bar-block">
                    <a href="/#" onClick={()=>SideFunction.side_close()} className="w3-bar-item w3-button w3-padding w3-text-teal"><i
                        className="fa fa-th-large fa-fw w3-margin-right"></i>HOME</a>
                    <a href="#about" onClick={()=>SideFunction.side_close()} className="w3-bar-item w3-button w3-padding"><i
                        className="fa fa-user fa-fw w3-margin-right"></i>ABOUT</a>
                    <a href="#contact" onClick={()=>SideFunction.side_close()} className="w3-bar-item w3-button w3-padding"><i
                        className="fa fa-envelope fa-fw w3-margin-right"></i>CONTACT</a>
                </div>
                <div className="w3-panel w3-large">
                    <a style={{color:"black"}} href='https://github.com/doncechenjun/'>
                    <i  style={{margin:"auto"}} className="fa fa-github w3-hover-opacity"></i>
                    </a>
                </div>
            </nav>
            <div className="w3-overlay w3-hide-large w3-animate-opacity" onClick={()=>SideFunction.side_close()} style={{cursor:"pointer"}}
            title="close side menu" id="myOverlay"></div>
      </div>
)}

export default SideBar