const Header=({SideFunction})=>{

    return(
        <header id="portfolio">
            <a href="/#"><img alt="myPhoto" src="a.jpg" style={{width:"65px"}} className="w3-circle w3-right w3-margin w3-hide-large w3-hover-opacity"/></a>
            <span className="w3-button w3-hide-large w3-xxlarge w3-hover-text-grey" onClick={()=>{SideFunction.side_open()}}><i className="fa fa-bars"></i></span>
        </header>
    )
}

export default Header